import React from "react";

function Table({ values }) {
  const { avalues, rvalues, errors, labels } = values;
  let totalError = 0;

  const prom = () => {
    return parseFloat(totalError / errors.length).toFixed(4);
  };

  return (
    <>
      <table className="table">
        <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col">Real</th>
            <th scope="col">Aproximado</th>
            <th scope="col">Error relativo</th>
          </tr>
        </thead>
        <tbody>
          {errors.map((data, index) => {
            totalError += data;
            return (
              <tr>
                <th scope="row">{labels[index]}</th>
                <td>{rvalues[index]}</td>
                <td>{avalues[index]}</td>
                <td className={data > 2 ? "text-danger" : "text-success"}>
                  {data}
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>

      <span className={prom() > 2 ? "text-danger" : "text-success"}>
        Error promedio: {prom()}%
      </span>
    </>
  );
}

export default Table;
