import { useState } from "react";
import Header from "./components/Header";
import Graph from "./components/Graph";
import Table from "./components/Table";

function App() {
  const inicialValues = [1, 5, 7, 10];
  // [li, ls, vli, n]

  const [numeros, setNumeros] = useState({
    li: inicialValues[0],
    ls: inicialValues[1],
    vli: inicialValues[2],
    n: inicialValues[3],
  });

  const { li, ls, vli, n } = numeros;

  const funcion = (t, y) => {
    return 10 - y / t;
  };

  const data = () => {
    let labels = [parseFloat(li)];
    let h = Math.abs(ls - li) / n;
    let y = parseFloat(vli);
    let avalues = [y];
    let rvalues = [y];
    let errors = [0];

    for (let i = 0; i < n; i++) {
      let last = labels[labels.length - 1];
      let y1 = funcion(last, y);
      let y2 = y1 * h + y;
      let y3 = funcion(last + h, y2);
      y = y + (y3 + y1) * (h / 2);
      avalues.push(parseFloat(y.toFixed(4)));
      labels.push(parseFloat((last + h).toFixed(4)));

      let yr = 5 * (last + h) + 2 / (last + h);
      rvalues.push(parseFloat(yr.toFixed(4)));

      errors.push(parseFloat(((Math.abs(y - yr) / yr) * 100).toFixed(4)));
    }

    return { avalues, rvalues, errors, labels };
  };

  const handleChange = (e) => {
    setNumeros({
      ...numeros,
      [e.target.name]: parseFloat(e.target.value),
    });
  };

  const values = data();

  return (
    <div className="d-flex justify-content-center align-items-center flex-column flex-wrap">
      <Header />
      <div className="row w-100">
        <div
          id="form"
          className="col-sm-3 d-flex justify-content-center flex-column mb-4"
        >
          <div className="form-floating mb-3">
            <input
              type="number"
              className="form-control"
              id="li"
              placeholder="1"
              name="li"
              value={isNaN(li) ? 0 : li}
              onChange={handleChange}
            />
            <label htmlFor="li">Lmite inferior</label>
          </div>
          <div className="form-floating mb-3">
            <input
              type="number"
              className="form-control"
              id="ls"
              placeholder="1"
              name="ls"
              value={isNaN(ls) ? 0 : ls}
              onChange={handleChange}
            />
            <label htmlFor="ls">Lmite superior</label>
          </div>
          <div className="form-floating mb-3">
            <input
              type="number"
              className="form-control"
              id="vli"
              placeholder="name@example.com"
              name="vli"
              value={isNaN(vli) ? 0 : vli}
              onChange={handleChange}
            />
            <label htmlFor="vli">Valor en lmite inferior</label>
          </div>
          <div className="form-floating mb-3">
            <input
              type="number"
              className="form-control"
              id="n"
              placeholder="name@example.com"
              name="n"
              value={isNaN(n) ? 0 : n}
              onChange={handleChange}
              min="1"
            />
            <label htmlFor="n">Nmero de saltos</label>
          </div>
          <button
            className="btn btn-primary"
            data-bs-toggle="modal"
            data-bs-target="#tableModal"
          >
            Ver tabla
          </button>
        </div>
        <div className="col-sm-9">
          <Graph values={values} />
        </div>
      </div>
      <div className="modal fade" id="tableModal">
        <div className="modal-dialog modal-dialog-centered modal-lg">
          <div className="modal-content">
            <div className="modal-header">
              <h5>Tabla de valores</h5>
              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <div className="modal-body">
              <Table values={values} />
            </div>
            <div className="modal-footer">
              <button
                type="button"
                className="btn btn-danger"
                data-bs-dismiss="modal"
              >
                Cerrar
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
